
import Vue from 'vue'
import App from './App'
import router from './router'
import 'babel-polyfill'//兼容es6代码
import Es6Promise from 'es6-promise' //兼容es6代码
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import store from './vuex/store'
// 引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
Vue.use(ElementUI);
require('es6-promise').polyfill()
Es6Promise.polyfill()

// import store from './vuex/store'
import axios from 'axios'
// import  localStorage, { setItem }  from './libs/common.js'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// import VueResource from 'vue-resource'; 
// Vue.use(VueResource); 

Vue.prototype.$axios = axios
// Vue.prototype.setItem=localStorage.setItem;
Vue.prototype.appName = '海多医疗'; 

//对导入的组件进行全局组件注册
// Object.keys(components).forEach((key) => {
// 	Vue.component(key, components[key])
// });

//添加路由钩子修改每个页面标题 
// router.beforeEach((to, form, next) => {
  
//   //如果自定义了标题就取标题，否则拿全局标题
//   window.document.title = to.meta.title !== undefined ? (to.meta.title + '-' + Vue.prototype.appName) : Vue.prototype.appName;
//   next()
