Vue.prototype.bus = new Vue;

router.afterEach(function (route) {
  console.log(route)
  let cname = "";
  switch (route.name) {
    case "index":
      cname = "主页";
      break;
    case "articleCategory":
      cname = "文章分类";
      break;
    case "articleComment":
      cname = "文章评价";
      break;
    case "articleList":
      cname = "文章列表";
      break;
    case "couponCategory":
      cname = "优惠券分类";
      break;
    case "couponList":
      cname = "优惠券列表";
      break;
    case "banner":
      cname = "幻灯片管理";
      break;
    case "directCategory":
      cname = "分类管理";
      break;
    case "directseeding":
      cname = "直播间管理";
      break;
    case "deirctIndex":
        cname = "互动直播";
        break;
    case "service":
        cname = "服务";
        break;
    case "doctorCategory":
        cname = "名医分类";
        break;        
    case "comment":
        cname = "评价";
          break;
    case "doctorList":
          cname = "医生列表";
          break;
    case "evaluationList":
          cname = "测评列表";
          break; 
    case "evaluationListRecord":
          cname = "测评记录";
          break;
    case "evaluationResult":
          cname = "测评结果";
          break;
    case "goodsCategory":
          cname = "测评分类";
          break;
    case "goodsComment":
          cname = "商品评价";
          break;
    case "goodsList":
          cname = "商品列表";
          break;
    case "commentInfo":
          cname = "评价通知";
          break;
    case "zanInfo":
          cname = "点赞通知";
          break;
    case "healthyList":
          cname = "健康支付订单列表";
          break; 
    case "shopList":
          cname = "商城订单列表";
          break; 
    case "userList":
          cname = "用户列表";
          break;             
    default:
      cname = "其他";
      break;
  }
  store.dispatch("HISTORY", {name: route.name, cname: cname});
});

/* eslint-disable no-new */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');



